import { Flex, Heading, HStack, Image, Text, useBreakpointValue } from "@chakra-ui/react";
import React from "react";
import { useTranslation } from "react-i18next";
import CallButton from "./CallButton";
import Socials from "./Socials";

const CallUs = ({ colorScheme }: { colorScheme?: "beige"; }) => {
    const { t } = useTranslation();
    const isBaseSize = useBreakpointValue(
        { base: true, md: false },
        { ssr: false }
    );

    const bgColor = colorScheme === "beige" ? "#664D33" : "#171717";

    return (
        <Flex
            w="100%"
            h="100%"
            alignItems="center"
            justifyContent="center"
            borderRadius="3xl"
            bgColor={bgColor}
            px={isBaseSize ? 8 : 16}
            py={isBaseSize ? 8 : 20}
            gridGap={isBaseSize ? 4 : 10}
            direction="column"
        >
            <Heading
                as="h1"
                size={isBaseSize ? "lg" : "3xl"}
                color="white"
                fontWeight="bold"
                textAlign="center"
            >
                {t("callUsForConsultation")}
            </Heading>
            <Text
                textAlign="center"
                fontSize={isBaseSize ? "sm" : "2xl"}
                my={4}
                color="rgba(255, 255, 255, 0.8)"
            >
                {t("teamOfExperts")}
            </Text>
            <HStack spacing={4}>
                <CallButton />
                <Socials />
            </HStack>
        </Flex>
    );
};

export default CallUs;
