import { Flex, Icon, Button, useBreakpointValue, Menu, MenuButton, MenuList, MenuItem, Image } from "@chakra-ui/react";
import { ChevronDownIcon }  from "@chakra-ui/icons";
import React from "react";
import { useTranslation } from "react-i18next";
import Socials from "./Socials";
import CallButton from "./CallButton";
import { ReactComponent as Logo } from "./logo.svg";

const supportedLanguages: {
    [key: string]: {
      nativeName: string;
    };
  } = {
    en: { nativeName: 'English' },
    ee: { nativeName: 'Eesti' },
    ru: { nativeName: 'Русский' },
};

const Header = () => {
    const { i18n } = useTranslation();
    const isBaseSize = useBreakpointValue(
        { base: true, md: false },
        { ssr: false }
    );

    return (
        <Flex
            justifyContent="space-between"
            alignItems="center"
            w="100%"
            h="100%"
        >
            <Icon as={Logo} width={isBaseSize ? "3rem" : "6rem"} height={isBaseSize ? "3rem" : "6rem"} />
            {!isBaseSize && <Socials hasBoxShadow={true} />}
            <Flex gridGap={4} alignItems="center" justifyContent="center">
                <Menu>
                    <MenuButton
                        as={Button}
                        rightIcon={<ChevronDownIcon />}
                        size={isBaseSize ? "sm" : "lg"}
                        borderRadius="full"
                    >
                        {supportedLanguages[i18n.resolvedLanguage || "en"].nativeName}
                    </MenuButton>
                    <MenuList>
                        {Object.keys(supportedLanguages).map((language) => (
                            <MenuItem
                                key={language}
                                style={{
                                    fontWeight: i18n.resolvedLanguage === language ? 'bold' : 'normal'
                                }}
                                onClick={() => i18n.changeLanguage(language)}
                            >
                                {supportedLanguages[language].nativeName}
                            </MenuItem>
                        ))}
                    </MenuList>
                </Menu>
                <CallButton />
            </Flex>
        </Flex>
    );
};

export default Header;
